.header{
    height:60px;
    display: flex;
    align-items: center;
    background-color: #131921;
    position: sticky;
    top: 0px;
    z-index: 100;
}

.header_logo{
    width: 100px;
    object-fit: contain;
    margin : 0 20px;
    margin-top: 18px;
    }

.header_search{
    display:flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;
}

.header_searchInput{
    height:12px;
    padding: 10px;
    border:none;
    width:100%;
}

.header_nav{
    display: flex;
    justify-content: space-evenly;
}

.header_optionLineOne{
    font-size:12px;
}

.header_optionLineTwo{
    font-size: 15px;
    font-weight: 800;
}

.header_searchIcon{
    padding: 5px;
    height: 22px !important;
    background-color: #cd9042;
}

.header_option{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right:10px;
    color: white;
}

.header_optionBasket{
    color: white;
    display: flex;
    align-items: center;

}

.header_basketCount{
    margin-left: 10px;
    margin-right: 10px;
}